<template>
    <div>
        <table class="table table-bordered table-hover table-responsive table-striped">
            <caption class="text-center"><h2>报表展示</h2></caption>
            <tbody>
                <tr>
                    <th>自动生成表单名称</th>
                    <th>收件人</th>
                    <th>抄送人</th>
                    <!--TODO 需要确定时间-->
                    <th>发送时间</th>
                    <!--<th>发送频率</th>-->
                    <th>
                        <!--<button>修改</button>-->
                        <!--<button>删除</button>-->
                        <!--<button>详细</button>-->
                        <!--<button>预览</button>-->
                      状态
                    </th>
                </tr>
                <tr v-for="report_generation in report_generation_list" :key="report_generation">
                    <td>{{ report_generation.workbook_name }}</td>
                    <td>{{ report_generation.receiver }}</td>
                    <td>{{ report_generation.cc }}</td>
                    <td>{{ report_generation.send_time }}</td>
                    <!--<td>{{ report_generation.frequency }}</td>-->
                    <td>
                        <button class="btn btn-warning btn-xs" v-on:click="report_generation_edit(report_generation)">修改</button>
                        <button class="btn btn-danger btn-xs" v-on:click="report_generation_del(report_generation)">删除</button>
                        <button class="btn btn-primary btn-xs" v-on:click="workbook_detail(report_generation)">详细</button>
                        <button class="btn btn-primary btn-xs" v-on:click="download_custom(report_generation)">预览</button>
                    </td>
                </tr>
                <tr>
                    <td colspan="6" class="text-center">
                        <button class="btn btn-primary" v-on:click="report_generation_add()">新增</button>
                        <a class="btn btn-success" href="/xadmin/">管理网站</a>
                    </td>
                </tr>
            </tbody>
        </table>
        <div class="text-center">
            <ul class="pagination">
                <li v-show="page_index >1" v-on:click="PageClick(page_index - 1)"><a>上一页</a></li>
                <li v-for="index in page_max" :key="index" v-bind:class="{active: page_index === index}"
                    v-on:click="PageClick(index)">
                        <a href="#">{{ index }}</a>
                </li>
                <li v-show="page_index<page_max" v-on:click="PageClick(page_index + 1)"><a>下一页</a></li>
                <li><a>共 {{page_max}} 页, 当前第 {{ page_index }} 页</a></li>
            </ul>
        </div>
        <!--详细 modal-->
        <Modal v-bind:show="isModalVisibleDetail" v-on:close="closeModal">
            <div slot="header">
                <h4>{{modal_workbook_ins.workbook_name}}</h4>
            </div>
            <div class="content" slot="body">
                <sheets-name v-bind:WorkbookIns="modal_workbook_ins"></sheets-name>
            </div>
        </Modal>
        <!--新增/修改 modal-->
        <Modal v-bind:show="isModalVisibleAdd" v-on:close="closeModal">
            <div slot="header">
                <h4>请填写报表数据</h4>
            </div>
            <div class="content" slot="body">
                <form  action="#"
                       @submit.prevent="report_generation_add_ok(report_generation)"
                       method="post">
                    <div class="form-group">
                        <label for="workbook_name">自动生成表单名称</label>
                        <input v-model="report_generation['workbook_name']" id="workbook_name"
                               type="text" class="form-control"  placeholder="请输入" required>
                        <label for="receiver">收件人</label>
                        <input v-model="report_generation['receiver']" id="receiver"
                               type="email" class="form-control"  placeholder="请输入" required>
                        <label for="cc">抄送人</label>
                        <input v-model="report_generation['cc']" id="cc"
                               type="text" class="form-control"  placeholder="请输入" required>
                        <label for="send_time">发送时间</label>
                        <input v-model="report_generation['send_time']" id="send_time"
                               type="date" class="form-control"  placeholder="请输入" required>
                        <!--<label for="frequency">发送频率</label>-->
                        <!--<input v-model="report_generation['frequency']" id="frequency"-->
                               <!--type="text" class="form-control"  placeholder="请输入" required>-->
                        <div class="text-center justify-content-center">
                            <button type="submit" class="btn btn-primary" aria-label="Close modal">确认</button>
                        </div>
                    </div>
                </form>
            </div>
        </Modal>
    </div>
</template>

<script>
import SheetsName from './SheetName'
import Modal from './Modal'
import axios from 'axios'
import qs from 'qs'
export default {
  name: 'work-book',
  components: {Modal, SheetsName},
  data () {
    return {
      // 初始化
      page_index: 1,
      page_count_max: 10,
      page_max: 10,
      isModalVisibleDetail: false,
      isModalVisibleAdd: false,
      modal_workbook_ins: '',
      report_generation: {},
      report_generation_list: []
    }
  },
  // created: function () {
  //   // 调用 json 接口,初始化数据
  //   // `this` 指向 vm 实例
  //   console.log('a is: ' + this.a)
  // },
  methods: {
    PageClick: function (index) {
      this.page_index = index
      this.GetWorkbookList()
    },
    report_generation_add: function () {
      this.showModalAdd()
    },
    report_generation_edit: function (ReportGenerationIns) {
      this.report_generation = JSON.parse(JSON.stringify(ReportGenerationIns))
      this.showModalAdd()
    },
    report_generation_add_ok: function (ReportGenerationIns) {
      // console.log(typeof (ReportGenerationIns.id))
      axios.post('/report_generation/workbook_info_id=0', qs.stringify({
        'ReportGenerationIns': ReportGenerationIns
      }))
      // TODO 执行顺序异常
      this.GetWorkbookList()
      this.closeModal()
    },
    workbook_detail: function (ReportGenerationIns) {
      this.modal_workbook_ins = ReportGenerationIns
      this.showModalDetail()
    },
    report_generation_del: function (ReportGenerationIns) {
      axios.delete('/report_generation/workbook_info_id=' + ReportGenerationIns['id'])
        .then(response => {
          this.GetWorkbookList()
        })
      this.GetWorkbookList()
    },
    download_custom: function (ReportGenerationIns) {
      window.open('/report_generation/download_custom_id=' + ReportGenerationIns['id']) // 新开窗口下载
    },
    showModalDetail: function () {
      this.isModalVisibleDetail = true
    },
    showModalAdd: function () {
      this.isModalVisibleAdd = true
    },
    closeModal: function () {
      this.isModalVisibleDetail = false
      this.isModalVisibleAdd = false
      this.report_generation = {}
      this.GetWorkbookList()
    },
    GetWorkbookList: function () {
      axios.get('/report_generation/workbook_list_info_page=' + this.page_index)
        .then(response => {
          this.page_max = response.data['page_max']
          this.page_count_max = response.data['page_count_max']
          // this.page_index = response.data['page_index']
          this.report_generation_list = []
          response.data['page_info'].forEach(
            one => {
              this.report_generation_list.push(one)
            }
          )
        })
    }
  },
  created: function () {
    this.GetWorkbookList()
  }
}

</script>

<style scoped>

</style>



