<template>
    <div>
        <table class="table table-bordered table-hover table-responsive table-striped">
            <!--<caption class="text-center"><h2>{{WorkbookIns}}</h2></caption>-->
            <col width="200" span="3">
            <tbody>
            <tr>
                <th class="text-center">sheet名称</th>
                <th class="text-center">列名称</th>
                <!--<th class="text-center">筛选条件</th>-->
                <th class="text-center">
                    <!--<button>修改</button>-->
                    <!--<button>删除</button>-->
                    <!--<button>详细</button>-->
                    状态
                </th>
            </tr>
            <tr v-for="sheet in sheet_list" :key="sheet">
                <td class="text-center" style="text-align: center">{{ sheet.sheet_name }}</td>
                <td class="text-center">{{ sheet.column_name_list }}</td>
                <!--<td class="text-center">{{ sheet.filter_column }}</td>-->
                <td class="text-center">
                    <button class="btn btn-warning btn-xs" v-on:click="sheet_edit(sheet)">修改</button>
                    <button class="btn btn-danger btn-xs" v-on:click="sheet_del(sheet)">删除</button>
                    <button class="btn btn-primary btn-xs" v-on:click="sheet_detail(sheet)">筛选条件查看</button>
                </td>
            </tr>
            <tr>
                <td colspan="4" class="text-center">
                    <button class="btn btn-primary" v-on:click="sheet_add()">新增</button>
                </td>
            </tr>
            </tbody>
        </table>
        <div class="text-center">
            <ul class="pagination">
                <li v-show="page_index >1" v-on:click="PageClick(page_index - 1)"><a>上一页</a></li>
                <li v-for="index in page_max" :key="index" v-bind:class="{active: page_index === index}"
                    v-on:click="PageClick(index)">
                    <a href="#">{{ index }}</a>
                </li>
                <li v-show="page_index<page_max" v-on:click="PageClick(page_index + 1)"><a>下一页</a></li>
                <li><a>共 {{page_max}} 页, 当前第 {{ page_index }} 页</a></li>
            </ul>
        </div>
        <!--详细 modal-->
        <Modal v-bind:show="isModalVisibleDetail" v-on:close="closeModal">
            <div slot="header">
                <h4>{{sheet_ins.sheet_name}}</h4>
            </div>
            <div class="content" slot="body">
                <sheet-name-detail v-bind:SheetIns="sheet_ins"></sheet-name-detail>
            </div>
        </Modal>
        <!--新增/修改 modal-->
        <Modal v-bind:show="isModalVisibleAdd" v-on:close="closeModal">
            <div slot="header">
                <h4>请填写报表数据</h4>
            </div>
            <div class="content" slot="body">
                <form  action="#"
                       @submit.prevent="sheet_add_ok(sheet_list_ins)"
                       method="post">
                    <div class="form-group">
                        <label for="sheet_name">sheet名称</label>
                        <input v-model="sheet_list_ins['sheet_name']" id="sheet_name"
                               type="text" class="form-control"  placeholder="请输入" required>
                        <label for="column_name_list">列名称</label>
                        <div>
                            <textarea v-model="sheet_list_ins['column_name_list']" id="column_name_list"
                                   type="text" class="form-control"  placeholder="请在下面的选择项进行勾选" disabled
                                      rows="3">
                            </textarea>
                            <label :for="column" class="checkbox-inline" v-for="column in get_column_title_list" :key="column">
                                <input type="checkbox" :id="column" :value="column" v-model="column_title_list" class="checkbox-inline">
                                {{ column }}
                            </label>
                            <!--<span>Checked names: {{ sheet_list_ins['column_name_list'] }}</span>-->
                        </div>
                        <div class="text-center justify-content-center">
                            <button type="submit" class="btn btn-primary" aria-label="Close modal">确认</button>
                        </div>
                    </div>
                </form>
            </div>
        </Modal>
    </div>
</template>

<script>
import SheetNameDetail from './SheetNameDetail'
import Modal from './Modal'
import axios from 'axios'
import qs from 'qs'
export default {
  name: 'sheets-name',
  components: {Modal, SheetNameDetail},
  props: ['WorkbookIns'],
  data () {
    return {
      // 初始化
      page_index: 1,
      page_count_max: 10,
      page_max: 10,
      sheet_list_ins: {},
      isModalVisibleDetail: false,
      isModalVisibleAdd: false,
      sheet_list: [],
      // 服务器接受
      get_column_title_list: [],
      // 用户选择列
      column_title_list: [],
      // 筛选条件
      filter_column_info: [],
      // sheet 实例
      sheet_ins: ''
    }
  },
  methods: {
    PageClick: function (index) {
      this.page_index = index
      this.GetSheetList()
    },
    GetSheetList: function () {
      // console.log(this.WorkbookIns)
      axios.get('/report_generation/sheet_list_info_' + 'id=' + this.WorkbookIns.id + '&page=' + this.page_index)
        .then(response => {
          this.page_max = response.data['page_max']
          this.page_count_max = response.data['page_count_max']
          // this.page_index = response.data['page_index']
          this.sheet_list = []
          response.data['page_info'].forEach(
            one => {
              this.sheet_list.push(one)
            }
          )
        })
    },
    GetColumnTitleList: function () {
      axios.get('/report_generation/get_column_title_list')
        .then(response => {
          this.get_column_title_list = response.data
        })
    },
    sheet_add: function () {
      this.column_title_list = []
      this.showModalAdd()
    },
    sheet_edit: function (SheetIns) {
      this.sheet_list_ins = JSON.parse(JSON.stringify(SheetIns))
      this.column_title_list = this.sheet_list_ins['column_name_list'].split(',')
      this.showModalAdd()
    },
    sheet_add_ok: function (SheetIns) {
      // console.log(typeof (ReportGenerationIns.id))
      SheetIns['workbook_ins_id'] = this.WorkbookIns.id
      if (SheetIns['column_name_list'].length === 0) {
        alert('列名称 必须选中一个及以上')
        return
      }
      SheetIns['column_name_list'] = SheetIns['column_name_list'].join(',')
      axios.post('/report_generation/sheet_info_id=0', qs.stringify({
        'SheetIns': SheetIns
      }))
      // TODO 执行顺序异常
      this.GetSheetList()
      this.closeModal()
    },
    sheet_detail: function (SheetIns) {
      this.sheet_ins = SheetIns
      this.showModalDetail()
    },
    sheet_del: function (SheetIns) {
      axios.delete('/report_generation/sheet_info_id=' + SheetIns['id'])
        .then(() => {
          this.GetSheetList()
        })
      // TODO 执行顺序异常
      this.GetSheetList()
    },
    showModalDetail: function () {
      this.isModalVisibleDetail = true
    },
    showModalAdd: function () {
      this.isModalVisibleAdd = true
      // 获取标题数据
      this.GetColumnTitleList()
    },
    closeModal: function () {
      this.isModalVisibleAdd = false
      this.isModalVisibleDetail = false
      this.sheet_list_ins = {}
      this.GetSheetList()
    }
  },
  watch: {
    'WorkbookIns': function () {
      this.GetSheetList()
    },
    'column_title_list': function () {
      this.sheet_list_ins['column_name_list'] = this.column_title_list
    }
  }
}
</script>

<style scoped>

</style>



