<template>
    <transition name="modal-fade" class="modal fade">
        <div class="modal-backdrop" @click="close" v-show="show">
            <div class="modal-dialog modal-content" @click.stop role="dialog" aria-labelledby="modalTile"
                 arial-describedby="modalDescription" style="width: 61.8%;"
                 >
                <div class="modal-header" id="modalTitle">
                    <button type="button" class="close" @click="close" aria-label="Close modal">&times;</button>
                    <slot name="header">
                        <h4>这是Modal弹框的标题</h4>
                    </slot>
                </div>

                <div class="modal-body" id="modalDescription">
                    <slot name="body">
                        这是Modal弹框的主体
                    </slot>
                </div>

                <div class="modal-footer">
                    <slot name="footer_ok"></slot>
                    <slot name="footer">
                        <!--这是Modal弹框的脚部-->
                        <button type="button" class="btn btn-default" @click="close" aria-label="Close modal">关闭</button>
                    </slot>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
export default {
  name: 'Modal',
  props: {'show': Boolean},
  data () {
    return {
    }
  },
  methods: {
    close: function () {
      this.show = !(this.show)
      this.$emit('close')
    }
  }
}
</script>

<style scoped>
    .modal-fade-enter,
    .modal-fade-leave-active {
        opacity: 70%;
    }
    .modal-fade-enter-active,
    .modal-fade-leave-active {
        transition: opacity 0ms ease
    }
    .modal-backdrop {
        background-color: rgba(0,0,0,.618);
    }
</style>



