<template>
    <div>
        <table class="table table-bordered table-hover table-responsive table-striped">
            <!--<caption class="text-center"><h2>{{WorkbookIns}}</h2></caption>-->
            <col width="200" span="4">
            <tbody>
            <tr>
                <th class="text-center">列名称</th>
                <th class="text-center">过滤关系</th>
                <th class="text-center">条件列表</th>
                <th class="text-center">
                    <!--<button>修改</button>-->
                    <!--<button>删除</button>-->
                    <!--<button>详细</button>-->
                    状态
                </th>
            </tr>
            <tr v-for="filter_col_info in filter_col_info_list" :key="filter_col_info">
                <td class="text-center" style="text-align: center">{{ filter_col_info.filter_col }}</td>
                <td class="text-center">{{ filter_col_info.filter_relation }}</td>
                <td class="text-center">{{ filter_col_info.condition_list }}</td>
                <td class="text-center">
                    <button class="btn btn-warning btn-xs" v-on:click="filter_col_edit(filter_col_info)">修改</button>
                    <button class="btn btn-danger btn-xs" v-on:click="filter_col_del(filter_col_info)">删除</button>
                    <!--<button class="btn btn-primary btn-xs" v-on:click="sheet_detail(filter_col_info)">筛选条件查看</button>-->
                </td>
            </tr>
            <tr>
                <td colspan="4" class="text-center">
                    <button class="btn btn-primary" v-on:click="filter_col_add()">新增</button>
                </td>
            </tr>
            </tbody>
        </table>
        <!--分页-->
        <div class="text-center">
            <ul class="pagination">
                <li v-show="page_index >1" v-on:click="PageClick(page_index - 1)"><a>上一页</a></li>
                <li v-for="index in page_max" :key="index" v-bind:class="{active: page_index === index}"
                    v-on:click="PageClick(index)">
                    <a href="#">{{ index }}</a>
                </li>
                <li v-show="page_index<page_max" v-on:click="PageClick(page_index + 1)"><a>下一页</a></li>
                <li><a>共 {{page_max}} 页, 当前第 {{ page_index }} 页</a></li>
            </ul>
        </div>
        <!--详细 modal-->
        <!--<Modal v-bind:show="isModalVisibleDetail" v-on:close="closeModal">-->
            <!--<div slot="header">-->
                <!--<h4>{{sheet_ins.sheet_name}}</h4>-->
            <!--</div>-->
            <!--<div class="content" slot="body">-->
                <!--<sheet-name-detail v-bind:SheetIns="sheet_ins"></sheet-name-detail>-->
            <!--</div>-->
        <!--</Modal>-->
        <!--新增/修改 modal-->
        <Modal v-bind:show="isModalVisibleAdd" v-on:close="closeModal" >
            <div slot="header">
                <h4>请填写报表数据</h4>
            </div>
            <div class="content" slot="body" style=" height: 600px;overflow-y: scroll;overflow-x: hidden;">
                <form  action="#"
                       @submit.prevent="filter_col_add_ok(filter_col_info_list_ins)"
                       method="post">
                    <div class="form-group">
                        <label for="filter_col">列名称</label>
                        <select id="filter_col"
                                v-model="filter_col_info_list_ins['filter_col']" required>
                            <option :for="column"  v-for="column in get_column_title_list" :key="column"
                                    :value="column" v-text="column"></option>
                        </select>
                        <label for="filter_relation">过滤关系</label>
                        <select id="filter_relation"
                                v-model="filter_col_info_list_ins['filter_relation']" required>
                            <option value=">=">大于等于</option>
                            <option value=">">大 于</option>
                            <option value="<=">小于等于 </option>
                            <option value="<">小 于</option>
                            <option value="is Null">值是否为空</option>
                            <option value="=">等 于</option>
                            <option value="like">包 含</option>
                            <option value="not like">不 包 含</option>
                        </select>
                        <label v-if="['>=', '>', '<', '<='].indexOf(filter_col_info_list_ins['filter_relation']) >= 0">
                            <input type="date" v-model="condition_list" >
                        </label>
                        <label v-if="['is Null'].indexOf(filter_col_info_list_ins['filter_relation']) >= 0">
                            <input type="radio" v-model="condition_list" value="True">是
                            <input type="radio" v-model="condition_list" value="False">否
                        </label>
                        <div class="text-center">
                            <label class="checkbox-inline" v-for="condition in get_column_condition_list" :key="condition"
                                   v-if="filter_col_info_list_ins['filter_relation'] === '='">
                                <input :id="condition" :value="condition" type="checkbox"
                                       v-model="condition_list" class="checkbox-inline"
                                >
                                <!--<input :id="condition" :value="condition" type="radio"-->
                                <!--v-model="condition_list" class="checkbox-inline"-->
                                <!--v-if="filter_column_info['filter_relation'] !== '='"-->
                                <!--&gt;-->
                                {{ condition }}
                            </label>
                        </div>
                    </div>
                    <div class="text-center justify-content-center">
                        <button type="submit" class="btn btn-primary" aria-label="Close modal">确认</button>
                    </div>
                </form>
            </div>
        </Modal>
    </div>
</template>

<script>
import axios from 'axios'
import qs from 'qs'
import Modal from './Modal'
export default {
  name: 'SheetNameDetail',
  components: {Modal},
  props: ['SheetIns'],
  data () {
    return {
      filter_col_info_list: [], // 服务器当前过滤列表
      page_index: 1, // 初始页面
      page_count_max: 10, // 初始页面
      page_max: 10, // 初始页面
      filter_col_ins: '', // filter_col 实例
      isModalVisibleDetail: false, // 详细 modal 状态
      isModalVisibleAdd: false, // 新增/修改 modal 状态
      filter_col_info_list_ins: {},
      //
      filter_column_info: {},
      filter_column_info_list: [],
      filter_column_info_str: '',
      // 用户选择
      condition_list: [],
      // 服务端接受
      get_column_condition_list: [],
      input_type_status: '',
      // 服务器接受
      get_column_title_list: []
    }
  },
  methods: {
    PageClick: function (index) {
      this.page_index = index
      this.GetFilterColList()
    },
    GetFilterColList: function () {
      // console.log(this.SheetIns)
      axios.get('/report_generation/filter_col_list_info_' + 'id=' + this.SheetIns.id + '&page=' + this.page_index)
        .then(response => {
          this.page_max = response.data['page_max']
          this.page_count_max = response.data['page_count_max']
          // this.page_index = response.data['page_index']
          this.filter_col_info_list = []
          response.data['page_info'].forEach(
            one => {
              this.filter_col_info_list.push(one)
            }
          )
        })
    },
    filter_col_add_ok: function (FilterInfoIns) {
      if (this.condition_list.length !== 0) {
        FilterInfoIns['condition_list'] = this.condition_list
        this.condition_list = []
      }
      // TODO 检查所有均已完成
      if (typeof (FilterInfoIns['filter_col']) === 'undefined') {
        alert('必须选择 列')
        return
      }
      if (typeof (FilterInfoIns['filter_relation']) === 'undefined') {
        alert('必须选择 关系')
        return
      }
      if (typeof (FilterInfoIns['condition_list']) === 'undefined') {
        alert('必须选择 条件')
        return
      }
      // if (Object.keys(FilterInfoIns).length !== 3) {
      //   alert('必须进行选择')
      //   return
      // }
      this.filter_column_info_list.push(FilterInfoIns)
      this.filter_column_info = {}
      console.log(this.filter_column_info_list)
      FilterInfoIns['sheet_ins_id'] = this.SheetIns.id
      FilterInfoIns['condition_list'] = JSON.stringify(FilterInfoIns['condition_list'])
      axios.post('/report_generation/filter_col_info_id=0', qs.stringify({
        'FilterInfoIns': FilterInfoIns
      }))
      // TODO 执行顺序异常
      this.GetFilterColList()
      this.closeModal()
      this.filter_col_info_list_ins = {}
    },
    filter_col_add: function () {
      this.condition_list = []
      this.showModalAdd()
    },
    filter_col_edit: function (FilterInfoIns) {
      this.filter_col_info_list_ins = JSON.parse(JSON.stringify(FilterInfoIns))
      // console.log(this.filter_col_info_list_ins)
      this.condition_list = JSON.parse(this.filter_col_info_list_ins['condition_list'])
      this.showModalAdd()
    },
    filter_col_del: function (FilterInfoIns) {
      axios.delete('/report_generation/filter_col_info_id=' + FilterInfoIns['id'])
        .then(() => {
          this.GetFilterColList()
        })
      // TODO 执行顺序异常
      this.GetFilterColList()
    },
    GetColumnTitleList: function () {
      axios.get('/report_generation/get_column_title_list')
        .then(response => {
          this.get_column_title_list = response.data
        })
    },
    GetColumnConditionList: function () {
      axios.get('/report_generation/get_column_condition_list_key=' + this.filter_col_info_list_ins['filter_col'])
        .then(response => {
          if (response.data !== 'fail') {
            this.get_column_condition_list = response.data
          } else {
            this.get_column_condition_list = []
          }
        })
    },
    showModalDetail: function () {
      this.isModalVisibleDetail = true
    },
    showModalAdd: function () {
      this.isModalVisibleAdd = true
      // 获取标题数据
      this.GetColumnTitleList()
    },
    closeModal: function () {
      this.isModalVisibleAdd = false
      this.isModalVisibleDetail = false
      this.filter_col_info_list_ins = {}
      this.GetFilterColList()
    }
  },
  watch: {
    'SheetIns': function () {
      this.GetFilterColList()
    },
    'filter_col_info_list_ins': {
      handler: function () {
        this.GetColumnConditionList()
      },
      deep: true
    }
  }
}
</script>

<style scoped>

</style>



